import { createRouter, createWebHashHistory} from "vue-router";

const routes = [
  // {
  //   path: '/',
  //   name: 'Welcome',
  //   component:() => import('@/views/welcome'),
  //   meta: {
  //     title: 'BSC'
  //   }
  // },
  {
    path: '/',
    name: 'Index',
    component:() => import('@/views/index'),
    meta: {
      title: '首页',
      // keepAlive: true
    }
  },
  {
    path: '/list',
    name: 'List',
    component: () => import('@/views/list'),
    meta: {
      title: '期权委托记录'
    }
  },
  {
    path: '/list/pool',
    name: 'ListPool',
    component: () => import('@/views/list/pool'),
    meta: {
      title: '矿池分红记录'
    }
  },
  {
    path: '/list/frozenpool',
    name: 'FrozenPool',
    component: () => import('@/views/list/Frozenpool'),
    meta: {
      title: '领取记录'
    }
  },
  {
    path: '/list/freeze',
    name: 'ListFreeze',
    component: () => import('@/views/list/freeze'),
    meta: {
      title: '冻结记录'
    }
  },
  {
    path: '/share',
    name: 'Share',
    component: () => import('@/views/share'),
    meta: {
      title: '邀请分享'
    }
  },
  {
    path: '/list/income',
    name: 'ListIncome',
    component: () => import('@/views/list/income'),
    meta: {
      title: '用户盈利信息'
    }
  },
  {
    path: '/pdf',
    name: 'PdfPage',
    component: () => import('@/views/pdf'),
    meta: {
      title: '白皮书'
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

router.beforeEach((to) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
})

export default router;