import { createApp } from 'vue'
import App from './App.vue'
import router from "./router";
import Vant from 'vant';
import store from "./store";
import 'vant/lib/index.css';
import { intro, locale } from "./storage"
import { i18n, setLang } from "./utils/i18n"
const lang = locale.get() || 'en'
setLang(lang)

// 返回上一页
const goBack = () => {
  if (history.length > 0) {
    router.go(-1) 
  } else {
    router.push({name: 'Welcome'}) 
  }
}
let queryParse = (str) => {
  if (!str || str === '0') {
    return {}
  }
  let dataArr = decodeURIComponent(str).split('&')
  let params = {}
  dataArr.forEach(query => {
    let queryItem = query.split('=')
    if (queryItem.length === 1) {
      params.id = queryItem[0]
    } else {
      params[queryItem[0]] = queryItem[1]
    }
  })
  return params
}

let href = window.location.href.replace(/#/g, '')
if (href.includes('?')) {
  let search = href.slice(href.indexOf('?') + 1, href.length)
  let params = queryParse(search)
  if (params.intro) {
    // 是否有推荐邀请码
    intro.set(params.intro)
  }
}

const app = createApp(App);
app.config.globalProperties.$goBack = goBack;
app
  .use(router)
  .use(store)
  .use(i18n)
  .use(Vant)
  .mount('#app');
