import store from "../store/index"
class Socket {
  constructor () {
    this.WSLock = false
    this.initWS()
  }
  initWS () {
  this.socket = window.io.connect('wss://ghpro-xs.dsceshi.cn');
  this.socket.emit("req", '')
  this.socket.emit("sub", "market")
  this.socket.on("close",  () => {
    this.reconnect()
  });
  this.socket.on("error", () => {
    this.reconnect()
  });
  this.socket.on("req", function (res){
    console.log('req---',res)
    let arr = []
    res.forEach((item) => {
      item.price = (item.price / 1e10).toFixed(2);
      let obj = Object.assign({tag: 3}, item)
      arr.unshift(obj)
    })
    store.commit('initChartData', arr)
  });
  this.socket.on("sub", (res) => {
    let price =  (res.price / 1e10).toFixed(5).slice(0,-1)
    store.commit('updatePrice', price)
    res.price = (res.price / 1e10).toFixed(2);
    let obj = Object.assign({tag: 3}, res)
    store.commit('updateChartData', obj)
  });
 }
 reconnect () {
    if (this.WSLock) return
    this.WSLock = true
    // 没连接上会一直重连，设置延迟避免请求过多
    setTimeout(() => {
        this.initWS()
        this.WSLock = false
    }, 4000)
  }
}
export default Socket;